import './css/HomePage.css'
import UserPanel from "../UserPanel/UserPanel";
import { Button , Modal } from 'antd';
import { useRef , useState } from 'react';
import { ExperimentFilled , TeamOutlined, TrophyFilled , HomeFilled, UserOutlined, SketchOutlined, DollarOutlined} from '@ant-design/icons'
import Title from 'antd/lib/skeleton/Title';

const Home = () => {
    const [modalIndex, setModalIndex] = useState(0);
    const [open, setOpen] = useState(false);
    const ModalContext = [
    <><div>
        <p style={{fontWeight:'900'}}>:</p>
        <p></p>
    </div></>,
    <><div>
        <p style={{fontWeight:'900'}}>:</p>
        <p></p>
    </div></>,
    <><div>
        <p style={{fontWeight:'900'}}>:</p>
        <p>()()</p>
    </div></>,
    <><div>
    <p style={{fontWeight:'900'}}>:</p>
    <p></p>
    </div></>,
    <><div>
    <p style={{fontWeight:'900'}}>:</p>
    <p>"/"""
        </p>
    </div></>,
    <><div>
    <p style={{fontWeight:'900'}}>:</p>
    <p>()</p>
    </div></>,]

    const next = ()=>{setModalIndex(i=>i+1)};
    const prev = ()=>{setModalIndex(i=>i-1)};
    const close = ()=>{setOpen(false);setModalIndex(0);};
    return (
        <>
        <Modal open={open} onClose={() => setOpen(false) } closable={false}
        footer={
            (modalIndex===0)?[<Button onClick={next}></Button>]:
            (modalIndex<ModalContext.length-1)?[<div style={{display:'flex',flexDirection:'row'}}>
            <Button onClick={prev} style={{width:'50%',alignSelf:'center'}}></Button>
            <Button onClick={next}  style={{width:'50%',backgroundColor:'orange'}}></Button></div>]:
            (modalIndex===ModalContext.length-1)?[<div style={{display:'flex',flexDirection:'row'}}>
            <Button onClick={prev} style={{width:'50%',alignSelf:'center'}}></Button>
            <Button onClick={close}  style={{width:'50%',backgroundColor:'orange'}}></Button></div>]:undefined
        }>
            <div style={{fontSize:'4vh'}}>{ModalContext[modalIndex]}</div>
        </Modal>
        <div id='HomePage'>
            <div id="Home">
                <p id='HomePageTitle'></p>
                <Button id='Instruction' onClick={()=>{setModalIndex(0);setOpen(true);}}>
                    
                </Button>
            </div>
            <div id="HomePageUI">
                <UserPanel/>
            </div>
        </div>
        </>
    )
}

export default Home;